.hero{
  width: 100%;
  height: 100vh;
  position: relative;
}
.hero-mid{
  width: 100%;
  height: 80vh;
  position: relative;
}
.hero-service{
  width: 100%;
  height: 80vh;
  position: relative;
}

.hero-mid .hide{
  display: none;
}

.hero-mid h1{
  padding-top: 4rem !important;
}

.hero-mid img{
  object-position: 75% 25%;
}

.hero-service img{
  object-position: 90% 5%;
  object-fit: cover;
}
.hero-service h1{
  padding-top: 5rem !important;
}
img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.hero-text{
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}

.hero-text h1{
  font-size: 3rem;
  font-weight: 800;
  background: #fff;
  -webkit-background-clip: text;
  -webkit-text-fill-color:transparent;
}

.hero-text p{
  font-size: 1.3rem;
  color: #fff; 
  padding: 0.5rem 0 2rem 0;
}

.hero-text .show{
  text-decoration: none;
  background: #fff;
  padding: 1rem 2rem;
  border-radius: 6px;
  font-size: 1.1rem;
  font-weight: bold;
  color: #222;
}

@media screen and (max-width: 555px){
  .hero-text h1 {
   padding: 10px 20px;
   font-size: 2.5rem;
  }

  .hero-text p{
    font-size: 1.1rem;
    padding: 0 0 2rem 0;
  }

  .hero-text .show{
    padding: 0.6rem 1.1rem;
    border-radius: 6px;
    font-size: 1rem;
  }
}